@import "../../../styles/index";

.hg-password-field {

  padding           : $field-padding;
  margin            : $field-margin;

  display           : flex;
  flex-direction    : row;
  align-items       : center;
  justify-content   : center;

  & > .hg-password-field-label {
    box-sizing: border-box;
    flex-shrink: 1;
    padding      : $field-label-padding;
    min-width: $field-label-min-width;
    text-align   : right;
  }

  & > .hg-password-field-input {
    box-sizing: border-box;
    padding: 1em 0.75em;
    flex-grow: 1;
    border: 0;
    font-size: 1.25em;
    border-radius: 0.25em;
    max-width: 100%;
    min-width: 0;
  }

}

